* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px
}

input {
  color: gray;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius:6px;
  margin: 10px auto;
  overflow: hidden;
  max-width: 350px;
}

.card-text {
  padding: 5px;
  text-align: center;
}

.card-img {
  max-height: 170px;

}